.submit-btn {
  color: #000;
  background-color: white;
  background-image: var(--bs-gradient);
  border-color: white;
  box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.15), 0 1px 1px rgba(0, 0, 0, 0.075);
